#resultCard {
  list-style-type: none;
  width: 80%;
  margin: 0 auto;
  padding: 0; }
  #resultCard #listItem {
    background-color: #f5f5f5;
    padding-left: 25px;
    padding-right: 25px;
    padding-bottom: 25px;
    padding-top: 1px;
    margin-bottom: 10px;
    position: relative; }
    #resultCard #listItem:hover {
      background-color: #ededed; }
    #resultCard #listItem a {
      position: absolute;
      left: 200px;
      color: #777; }
    #resultCard #listItem #resultTitle {
      font-size: 24pt;
      margin-top: 15px;
      margin-bottom: -15px; }
    #resultCard #listItem #resultCity {
      font-size: 16pt; }
    #resultCard #listItem #eventlist {
      margin-top: 20px;
      display: inline;
      list-style: none;
      margin-left: -40px; }
    #resultCard #listItem #eventlist li {
      display: inline; }
    #resultCard #listItem #eventlist li:after {
      content: ", "; }
    #resultCard #listItem #eventlist li:last-child:after {
      content: ""; }

